#carousel {
  transform-style: preserve-3d;
  display: contents;
}


.title {
  transform: translateY(-220px);
  font-size: 1.5rem;
  color: #271651;
}

.card {
  position: absolute;
  width: 160px;
  height: 200px;
  transform-style: preserve-3d;
  transition: 1s;
  border-radius: 15px;
  border: 1px solid #b0b0dc;
  transform: none;


  .slider-content {
    color: white;
    font-weight: 500;
    font-size: 0.6rem;
    list-style: none;
  }

  li {
    padding: 11%;
    margin-left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    line-height: 1.3rem;
    border-bottom: 1px solid #dbdbf3;

    .star {
      width: 10px;
      height: 10px;
      margin-right: 10px;
    }
  }

  .dot-container {
    margin-top: 10px;
    overflow: hidden;
    // display: flex;
  }

  .dot {
    display: inline-block;
    height: 14px;
    width: 14px;
    background-color: #f1ebf7;
    border-radius: 50%;
    margin: 0 4px;

    &:first-child {
      margin-left: 10px;
    }
  }

  &.active {
    border-radius: 10px;
    border-color: #5858c6;
    li {
      padding: 7% 7%;
      border-bottom: 1px solid #7979e4;
    }
    .dot {
      height: 9px;
      width: 8px;
    }

    .dot-container {
      margin-top: 2px;
    }
  }
}

.card:nth-child(1) {
  transform: translateX(0) translateZ(0);
  background-color: lightblue;
}

.card:nth-child(2) {
  transform: translateX(-55%) translateZ(-50px) scale(0.8);
  background-color: coral;
}

.card:nth-child(3) {
  transform: translateX(55%) translateZ(-50px) scale(0.8);
  background-color: chartreuse;
}

.indicators {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 10%;

  .dot {
    width: 15px;
    height: 15px;
    background-color: #ffffff;
    border-radius: 50%;
    margin-right: 1.5rem;
    cursor: pointer;
  }
  .active {
    background-color: #5858c6;
  }
}

.explore-btn {
  display: flex;
  flex-direction: row;
  height: 50px;
  transform: translateY(250px);
  width: 333px;
  color: white;
  cursor: pointer;

  .tail {
    background-color: #9db0c7;
    width: 16px;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
  }

  .main {
    background-color: #ef5da8;
    flex: 1;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .head {
    width: 0;
    height: 0;
    border-top: 25px solid transparent;
    border-bottom: 25px solid transparent;
    border-left: 45px solid #5e6770;
    margin-left: 7px;
    border-radius: 5px;
  }
}
