@import "../../styles/variables";
@import "../../styles/colors";

.footer {
  display: flex;
  background-color: #3b3b4c;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.16);
  flex-direction: row;
  color: white;
  padding: 1.5% 4%;
  justify-content: center;
  align-items: center;

  .quick {
    display: flex;
    flex-direction: row;
    flex: 1;
    font-weight: 400;
    font-size: 16px;

    > div {
      padding-left: 5%;
    }
  }

  .social {
    width: 20%;
    img {
      padding-left: 10%;
      cursor: pointer;
    }
  }
}
