.home-container {
  scroll-snap-type: y mandatory;
  font-size: 18px;
  height: 100%;
  background: linear-gradient(
      107.75deg,
      rgba(255, 255, 255, 0.2) 0%,
      rgba(239, 93, 168, 0.2) 29.54%,
      rgba(246, 165, 207, 0.110513) 54.46%,
      rgba(88, 88, 198, 0.2) 72.04%,
      rgba(88, 88, 198, 0.2) 89.88%
    ),
    #ffffff;

  .free-trial-btn {
    background-color: #f6b500;
    padding: 0.6rem 1rem;
    border-radius: 0;
    color: black;
  }

  .ml-2 {
    margin-left: 2.5rem;
  }

  .sec1-title {
    font-size: 3rem;
    line-height: 5rem;
    font-weight: bold;
    color: #271651;
    margin: 2rem 0;
    text-transform: capitalize;
  }

  .sec1-desc {
    font-size: 1.13rem;
    color: #2f2f89;
    line-height: 2.5rem;
    text-transform: capitalize;
  }

  .section2 {
    background-color: #ffe3dd;
  }

  .section3 {
    background: linear-gradient(
        0deg,
        rgba(88, 88, 198, 0.06),
        rgba(88, 88, 198, 0.06)
      ),
      #ffffff;
  }

  .section4 {
    background: linear-gradient(
        0deg,
        rgba(239, 93, 168, 0.06),
        rgba(239, 93, 168, 0.06)
      ),
      #ffffff;
  }

  .sec-image {
    width: 100%;
    height: 100%;
  }

  .play-container {
    position: relative;

    .action {
      position: absolute;
      bottom: 2.9%;
      display: flex;
      flex-direction: row;
      align-items: center;
      text-transform: capitalize;
      width: 100%;
      line-height: 2rem;
      cursor: pointer;
    }
  }

  .play-btn {
    width: 94px;
    height: 94px;
    background-color: #ef5da8;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-right: 5%;


    .play-icon {
      width: 0;
      height: 0;
      border-top: 15px solid transparent;
      border-bottom: 15px solid transparent;
      border-left: 25px solid white;
      margin-left: 7px;
    }
  }
}
