@import "../../styles/variables";
@import "../../styles/colors";

.splitContainer {
  height: 100vh;
  display: flex;
  flex-direction: row;
  scroll-snap-align: center;

  .left, .right {
    height: 100%;
    justify-content: center;
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 5%;
  }

  .right {
    align-items: center;
  }

}
